import styled from "styled-components";
import NormalizePrice from "../utils/NormalizePrice";
import BubbleAlert from "./BubbleAlert";

const Div = styled.div`
    display: flex;
    justify-content: flex-end;
    width: 40%;
    align-items: center;
    color: #006DE3;
    position: relative;
`

const P = styled.p`
    font-weight: 600;
    font-size: 0.9em;
`

const Img = styled.img`
    height: 21px;
    width 20px;
    padding-left: 8px;
`
const countTotalPrice = (cart) => {
    return cart.reduce((acc, el) => {
        const price = el.price * el.quantity;
        return acc + price;
    }, 0)
};

const CartShop = ({ cart }) => {
    const totalPrice =  cart.length ? countTotalPrice(cart) : 0;
    const totalProducts = cart.length ? cart.reduce((acc, el) => acc + el.quantity, 0) : 0;
    
    return (
        <Div>
            { totalPrice ? <P>{NormalizePrice(totalPrice)}</P> : null }
            <Img alt="cartshop" src="assets/shopping-cart.png"/>
            { totalProducts ? <BubbleAlert quantity={totalProducts}/> : null }
        </Div>
    )
}

export default CartShop;