import { useState, useRef, useCallback } from "react";
import styled from "styled-components";
import Navbar from "./components/Navbar";
import Container from "./components/Container";
import Title from "./components/Title";
import useProductSearch from "./customHooks/useProductSearch";
import { NetworkStatusProvider } from "./customHooks/useNetworkStatus";
import Product from "./components/Product";

const ProductsDiv = styled.div`
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; 
    margin: 0 auto;
    gap: 8px
`;


const App = () => {
  const [ cart, setCart ] = useState([]);
  const [ pageNumber, setPageNumber ] = useState(1);
  const { products, loading, hasMore } = useProductSearch(pageNumber);
  const observer = useRef(null);

  const lastElementRef = useCallback( node => {
    if(loading) return;
    if(observer.current) observer.current.disconnect();

    observer.current = new IntersectionObserver( ([entries]) => {
      if(entries.isIntersecting && hasMore) {
        setPageNumber( prevPageNumber => prevPageNumber + 1);
      }
    });

    if(node) observer.current.observe(node);
  }, [ loading, hasMore ]);

  const addToCart = (product) => {

    const hasProduct = cart.find( p => p.id === product.id);

    if(hasProduct) {
      const newCart = cart.map( p => {
        return p.id === product.id ? { ...p, quantity: p.quantity + 1} : p
      });
      
      return setCart(newCart);
    }

    return setCart(
      [
        ...cart,
        { ...product, quantity: 1 }
      ]
    );
  };

  const removeFromCart = (product) => {
    const newCart = cart.map( p => {
      return p.id === product.id ? { ...p, quantity: p.quantity - 1} : p
    });

    return setCart(newCart);
  }
 
  return (
    <div>
      <NetworkStatusProvider>
        <Navbar cart={cart}/>
        <Container>
          <Title/>
          <ProductsDiv>
            {
                products.map( (product, index) =>(

                  products.length === index + 1 ?
                    <Product
                    key={product.id}
                    ref={lastElementRef}
                    product={product}
                    cart={cart}
                    addToCart={addToCart}
                    removeFromCart={removeFromCart}
                    /> :
                    <Product
                    key={product.id}
                    product={product}
                    cart={cart}
                    addToCart={addToCart}
                    removeFromCart={removeFromCart}
                    />
                ))
            }
          </ProductsDiv>
          <div>{ loading && "Loading..."}</div>
        </Container>
      </NetworkStatusProvider>
    </div>
  )
}
export default App;
