import React from "react";
import styled from "styled-components";
import NormalizePrice from "../utils/NormalizePrice";
import ProductAction from "./ProductAction";

const Div = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #FFFF;
    max-width: 9rem;
    height: 280px;
    border-radius: 3px;
    padding: 8px;

    &:hover {
        box-shadow: 0 0 4px 0 rgba(0,0,0,0.08);
    }
`

const Description = styled.p`
    text-align: center;
    margin: 0;
    font-size: 0.9em;
`
const Price = styled.p`
    font-size: 1em;
    color: #0070E0;
    text-align: center;
    font-weight: 700;
    margin: 0;
`
const Img = styled.img`
    width: 100%
`
const OriginalPrice = styled.span`
    font-size: 0.8em;
    color: #000000;
    font-weight: 600;
    padding: 5px;
    text-decoration: line-through;
`
const Product = React.forwardRef(( { product, cart, addToCart, removeFromCart } , ref) => {
    const productInCart = cart.find( p => p.id === product.id );
    const quantity = productInCart?.quantity;
    const productPriceText = NormalizePrice(product.price);
    return (
        <Div ref={ref}>
            <div>
                <Img alt="img" src={product.photo}/>
            </div>
            <Description>{product.name}</Description>
            <Price>
                {
                    quantity ? 
                    `${quantity} x `:
                    (product.originalPrice !== product.price ? <OriginalPrice>{`$${product.originalPrice}`}</OriginalPrice> : null ) 
                }
                {productPriceText}
            </Price>
            <ProductAction
            addToCart={addToCart}
            removeFromCart={removeFromCart}
            product={product}
            quantity={quantity}
            />
        </Div>
    )
})

export default Product;