import styled from "styled-components";

const DivAction = styled.div`
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 32px;
`

const CartAction = styled.button`
    height: 100%;
    background: #FFFFFF;
    border: 1px solid #0070E0;
    border-radius: 3px;
    font-size: 0.8em;
    color: #0070E0;
    text-align: center;
`
const ActionBtn = styled.button`
    width: 32px;
    height: 32px;
    border-radius: 10%;
    background-color: #0070E0;
    color: #FFFF;
    border: none;
`
const QuantitySpn = styled.span`
    color: #0070E0;
    font-weight: 500;
`

const ProductAction = ({ addToCart, product, removeFromCart, quantity }) => {
    return(
        <DivAction>
            {
                quantity ?
                <DivAction>
                    <ActionBtn onClick={() => removeFromCart(product)}>-</ActionBtn>
                    <QuantitySpn>{ quantity }</QuantitySpn>
                    <ActionBtn onClick={() => addToCart(product)}>+</ActionBtn>
                </DivAction>:
                <CartAction onClick={() => addToCart(product)}>
                    Agregar al carrito
                </CartAction>
            }
        </DivAction>
    )
}

export default ProductAction;